import React from 'react';

import Typeahead from './typeahead';
import AllLocationsList from './all-locations-list';

/**
 * Returns the all cafes list view
 *
 * @param props
 * @returns {*}
 * @constructor
 */

const AllLocations = props => (
	<React.Fragment>
		<Typeahead {...props} />
		<AllLocationsList {...props} />
	</React.Fragment>
);

export default AllLocations;



