import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import _ from 'lodash';
import Geosuggest from 'react-geosuggest';
import autobind from 'autobind-decorator';

import { I18N } from '../globals/wp';
import { locationRoute } from '../utils/data/routes';

import GetLocation from './get-location';

import styles from './typeahead.pcss';

/**
 * The typeahead component. Powered by react geosuggest.
 */

export default class Typeahead extends Component {
	static propTypes = {
		handleUpdate: PropTypes.func,
		navigate: PropTypes.func,
		query: PropTypes.string,
	};

	static defaultProps = {
		handleUpdate: () => {},
		navigate: () => {},
		query: '',
	};

	componentDidMount() {
		document.addEventListener('modern_tribe/locator/update_query', this.handleQueryChange);
	}

	componentWillUnmount() {
		document.removeEventListener('modern_tribe/locator/update_query', this.handleQueryChange);
	}

	@autobind
	handleQueryChange(e) {
		this.suggest.update(e.detail.query);
		// geosuggest internals appear to require this delay after doing an update to select the suggestions it gens,
		// very annoying, they are working on these methods and lots of reports.
		_.delay(() => this.suggest.selectSuggest(), 10);
	}

	/**
	 * clears the input
	 * @param e
	 */
	@autobind
	clearInput(e) {
		e.preventDefault();
		this.suggest.update('');
		this.props.navigate(locationRoute());
	}

	/**
	 * Updates location on user selection of an address.
	 * @param data
	 */
	@autobind
	updateLocator(data) {
		this.props.navigate(locationRoute(data)).then(() => this.props.handleUpdate());
	}

	render() {
		const containerClasses = classNames({
			'location-search': true,
			[styles.container]: true,
		});
		const iconClasses = classNames({
			'location-search-icon': true,
			'icon': true,
			'icon-search': true,
			[styles.icon]: true,
		});
		const clearClasses = classNames({
			'icon': true,
			'icon-cross': true,
			[styles.clear]: true,
		});

		return (
			<div className={containerClasses}>
				<Geosuggest
					ref={(c) => { this.suggest = c; }}
					placeholder={I18N.location_input_placeholder}
					initialValue={decodeURIComponent(this.props.query)}
					className={styles.suggest}
					inputClassName={styles.input}
					onSuggestSelect={this.updateLocator}
					autoActivateFirstSuggest
				/>
				<button className={clearClasses} onClick={this.clearInput} />
				<i className={iconClasses} />
				<GetLocation
					{...this.props}
				/>
			</div>
		);
	}
}



