import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import autobind from 'autobind-decorator';

import Typeahead from './typeahead';
import ResultsList from './results-list';

import { SEARCH_TYPES } from '../globals/config';
import { locationsForQuery } from '../utils/data/locations-for-query';

export default class Results extends PureComponent {
	static propTypes = {
		country: PropTypes.string,
		lat: PropTypes.string,
		lng: PropTypes.string,
		location: PropTypes.object,
		navigate: PropTypes.func,
		query: PropTypes.string,
		state: PropTypes.string,
		type: PropTypes.string,
	};

	static defaultProps = {
		country: '',
		lat: '0',
		lng: '0',
		location: {},
		navigate: () => {},
		query: '',
		state: '',
		type: SEARCH_TYPES.distance,
	};

	constructor(props) {
		super(props);
		this.state = {
			locations: this.getLocations(props),
			type: props.type,
		};
	}

	componentWillReceiveProps(props) {
		// router acting strange on back button history, had to resort to this. They are they looking at some issues
		this.setState({
			locations: this.getLocations(props),
			type: props.type,
		});
	}

	/**
	 * Get the locations based on router props. Uses locationsForQuery
	 * @param props
	 */

	getLocations(props) {
		const { lat, lng, country, state, type } = props;
		return locationsForQuery(parseFloat(lat), parseFloat(lng), country, state, type);
	}

	/**
	 * Receives update triggers after address change in typeahed or get location
	 */

	@autobind
	handleUpdate() {
		this.setState({ locations: this.getLocations(this.props) });
	}

	render() {
		return (
			<React.Fragment>
				<Typeahead {...this.props} handleUpdate={this.handleUpdate} />
				<ResultsList {...this.props} {...this.state} />
			</React.Fragment>
		);
	}
}



