import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';

import { GOOGLE } from '../globals/config';
import { I18N, API_KEY } from '../globals/wp';

import styles from './results-list.pcss';

export default class ResultsList extends PureComponent {
	static propTypes = {
		locations: PropTypes.array,
	};

	static defaultProps = {
		locations: [],
	};

	locationImage(location) {
		const { lat, lng, address } = location;
		const src = `${GOOGLE.static_url}?center=${lat},${lng}&markers=color:blue|${address}&style=feature:poi|visibility:off&key=${API_KEY}&size=600x413`;
		return <img src={src} className={styles.img} alt={address} />;
	}

	mapLink(address) {
		return `${GOOGLE.map_url}?api=1&query=${address}`;
	}

	getAwardsList(location) {
		if (!location.awards || location.awards.length===0) {
			return;
		}
		const awards = _.map(location.awards, award=> (
			<li className={styles.awardsItem} key={location.ID}>
				<img src={award.image_src} className={styles.awardImage} alt={award.description} />
				<div className={styles.awardDescription}>{award.description}</div>
			</li>
		));
		return (<ul className={styles.awardsList}>{awards}</ul>);
	}
	
	getAward(award) {

	}

	results() {
		return _.map(this.props.locations, location => (
			<article className={styles.location} key={location.ID}>
				<div className={styles.image}>
					<a className={styles.imageLink} href={location.link}>
						{this.locationImage(location)}
					</a>
				</div>
				<div className={styles.info}>
					<h4 className={styles.heading}>
						<a className={styles.headingLink} href={location.link}>
							{location.title}
						</a>
					</h4>
					<address className={styles.address}>
						<a href={this.mapLink(location.address)} target="_blank">
							{location.address}
						</a>
					</address>
					<a className={styles.phone} href={`tel:${location.phone}`}>{location.phone}</a>
					{this.getAwardsList(location)}
				</div>
			</article>
		));
	}

	noResults() {
		return <article className={styles.noResults}>{I18N.no_results}</article>;
	}

	render() {
		return (
			<section className={styles.container}>
				{this.props.locations.length ? this.results() : this.noResults()}
			</section>
		);
	}
}



