import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'autobind-decorator';
import _ from 'lodash';

import { LOCATIONS, CITIES, STATES } from '../globals/wp';
import * as events from '../utils/events';

import styles from './all-locations-list.pcss';


/**
 * Render the cafe items below the typeahead
 */

export default class AllLocationsList extends Component {
	static propTypes = {
		location: PropTypes.object,
		navigate: PropTypes.func,
	};

	static defaultProps = {
		location: {},
		navigate: () => {},
	};

	getLocationsByCity(city) {
		const cafes = _.filter(LOCATIONS, cafe => this.isInCurrentCity(cafe, city));
		const sorted = _.orderBy(cafes, [cafe => cafe.title], ['asc']);
		return _.map(sorted, cafe => (
			<li key={cafe.title}>
				<a className={styles.link} href={cafe.link}>{cafe.title}</a>
			</li>
		));
	}

	getLocationsByState(state) {
		const locations = _.filter(LOCATIONS, location => this.isInCurrentState(location, state.code));
		const sorted = _.orderBy(locations, [location => location.title], ['asc']);
		return _.map(sorted, location => (
			<li key={location.title}>
				<a className={styles.link} href={location.link}>{location.title}</a>
			</li>
		));
	}

	getCities() {
		const sorted = _.orderBy(CITIES, [city => city.name], ['asc']);
		return _.map(sorted, city => (
			<div className={styles.group} key={city.name}>
				<button className={styles.category} data-query={city.search_string} onClick={this.handleCityClick}>{city.name}</button>
				<ul className={styles.list}>{this.getLocationsByCity(city.name)}</ul>
			</div>
		));
	}

	getStates() {
		const sorted = _.orderBy(STATES, [state => state.name], ['asc']);
		return _.map(sorted, state => (
			<div className={styles.group} key={state.code}>
				<button className={styles.category} data-query={state.search_string} onClick={this.handleCityClick}>{state.name}</button>
				<ul className={styles.list}>{this.getLocationsByState(state)}</ul>
			</div>
		));
	}

	isInCurrentCity(cafe, city) {
		return cafe.city === city;
	}

	isInCurrentState(location, stateCode) {
		return location.state === stateCode;
	}

	getOrganizedLocations() {
		if (STATES.length) {
			return this.getStates();
		} else {
			return this.getCities();
		}
	}

	@autobind
	handleCityClick(e) {
		events.trigger({ event: 'modern_tribe/locator/update_query', native: false, data: { query: e.target.dataset.query } });
	}

	render() {
		return (
			<section className={styles.container}>
				<article className={styles.content}>
					{ this.getOrganizedLocations() }
				</article>
			</section>
		);
	}
}



